*
{
    margin: 0;
    padding: 0;
    /* overflow: hidden; */


}


.webgl
{
    position: fixed;
    top: 0;
    left: 0;
    outline: none;
    z-index: -1;
}



#container {
    z-index: 100;
    position: relative;
    padding: 50px;
    font-family: 'sizhan', sans-serif;

  }

  #container canvas, #overlay {
    /* font-family: 'Mea Culpa', cursive; */

    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
 
    /*
      Introduced in IE 10.
      See http://ie.microsoft.com/testdrive/HTML5/msUserSelect/
    */
    -ms-user-select: none;
    user-select: none;
    
    font-size: 4vh;
    color: rgb(255, 255, 255);
    position: absolute;
  }
  canvas {
    border: 1px solid black;
  }

  .row{
    padding-top: 20%;
    padding-left: 2%;
    user-select: none;
  }
  .column{
    user-select: none;
    font-family: 'sizhan', sans-serif;
    color: white;
    float: left;
    width: 60%;
    border: solid 1px white;
  }


